// frontend/src/components/CookieModal.js
import React from 'react';
import { X } from 'lucide-react';
import { getVulnerabilityExplanation } from '../utils/cookieUtils';

const CookieModal = ({ cookie, isOpen, onClose }) => {
    if (!cookie || !isOpen) return null;

    return (
        // Modal Overlay handles close on backdrop click
        <div id="cookie-modal-overlay" className={`modal-overlay ${isOpen ? 'open' : ''}`} onClick={onClose}>
            {/* Modal Box stops click propagation */}
            <div id="cookie-modal-box" className="modal-box" onClick={e => e.stopPropagation()}>
                <div className="flex justify-between items-center mb-4">
                    <h3 className="text-xl font-semibold text-white" id="modal-title">Cookie Details</h3>
                    <button id="modal-close-btn" className="text-gray-400 hover:text-white" onClick={onClose}>
                        <X size={24} />
                    </button>
                </div>
                <div id="modal-content" className="space-y-4">
                    <div>
                        <span className="text-sm text-gray-400">Name</span>
                        <p className="text-lg font-medium text-white">{cookie.name}</p>
                    </div>
                    <div>
                        <span className="text-sm text-gray-400">Domain</span>
                        <p className="text-lg font-medium text-white">{cookie.domain}</p>
                    </div>
                    <div>
                        <span className="text-sm text-gray-400">Category</span>
                        <p className="text-lg font-medium text-white">{cookie.category}</p>
                    </div>
                    <div>
                        <span className="text-sm text-gray-400">Purpose</span>
                        <p className="text-gray-300">{cookie.purpose}</p>
                    </div>
                    <div>
                        <span className="text-sm text-gray-400">Expiration</span>
                        <p className="text-gray-300">{cookie.expiration}</p>
                    </div>
                    <div>
                        <span className="text-sm text-gray-400">Vulnerabilities</span>
                        {cookie.vulnerabilities.length === 0 
                            ? <p className="text-green-400">No vulnerabilities detected.</p>
                            : <ul className="list-disc list-inside mt-1 space-y-1">
                                    {cookie.vulnerabilities.map((v, i) => (
                                        <li key={i} className="text-yellow-400" 
                                            dangerouslySetInnerHTML={{ __html: getVulnerabilityExplanation(v) }} 
                                        />
                                    ))}
                              </ul>
                        }
                    </div>
                </div>
            </div>
        </div>
    );
};

export default CookieModal;