// frontend/src/components/CookieRow.js
import React from 'react';
import { getInsightBadges } from '../utils/cookieUtils';

const CookieRow = ({ cookie, isSelected, onSelect, onViewDetails, isDeleting }) => {
    
    const handleClick = (e) => {
        if (e.target.type !== 'checkbox') {
            onViewDetails(cookie.id);
        }
    };

    const handleCheckboxChange = (e) => {
        onSelect(cookie.id, e.target.checked);
    };

    const rowClasses = `hover:bg-gray-700 cursor-pointer ${isDeleting ? 'fade-out' : ''}`;

    return (
        <tr className={rowClasses} data-cookie-id={cookie.id} onClick={handleClick}>
            <td className="p-3">
                <input 
                    type="checkbox" 
                    className="cookie-checkbox" 
                    data-id={cookie.id} 
                    data-category={cookie.category} 
                    checked={isSelected}
                    onChange={handleCheckboxChange}
                />
            </td>
            <td className="p-3">{cookie.name}</td>
            <td className="p-3">{cookie.domain}</td>
            <td className="p-3">{cookie.category}</td>
            <td className="p-3 flex flex-wrap gap-1">{getInsightBadges(cookie.vulnerabilities)}</td>
        </tr>
    );
};

export default CookieRow;