// frontend/src/components/CookieDashboard.js
import React, { useState, useMemo, useCallback } from 'react';
import { mockCookies } from '../data/mockData.js';
import { fixedScoreData } from '../utils/cookieUtils.js';
import InfoBar from './InfoBar.js';
import CategoryPanel from './CategoryPanel.js';
import CookieTable from './CookieTable.js';
import CookieModal from './CookieModal.js';
import { XCircle } from 'lucide-react';

function CookieDashboard() {
    // --- State Management ---
    const [cookies, setCookies] = useState(mockCookies); 
    const [active_categories, setActiveCategories] = useState(['Essential', 'Preference', 'Analytics', 'Tracking']);
    const [selectedCookieId, setSelectedCookieId] = useState(null);
    const [actionMessage, setActionMessage] = useState('');
    const [isBannerOpen, setIsBannerOpen] = useState(true);
    const [cookiesToDelete, setCookiesToDelete] = useState([]); // For animation

    // --- Computed Values ---
    const filteredCookies = useMemo(() => {
        return cookies.filter(cookie => active_categories.includes(cookie.category));
    }, [cookies, active_categories]);
    
    // Use fixed mock score data for visuals
    const { privacy_score, privacy_rank, score_colour, riskBadgeClass, riskBadgeText } = fixedScoreData;
    
    const currentCookie = useMemo(() => {
        return cookies.find(c => c.id === selectedCookieId);
    }, [cookies, selectedCookieId]);

    // --- Handlers ---
    
    const showMessage = useCallback((message) => {
        setActionMessage(message);
        setTimeout(() => setActionMessage(''), 2000);
    }, []);

    const handleToggleCategory = useCallback((category, isChecked) => {
        setActiveCategories(prev => {
            if (isChecked && !prev.includes(category)) {
                return [...prev, category];
            }
            if (!isChecked && prev.includes(category)) {
                return prev.filter(c => c !== category);
            }
            return prev;
        });
    }, []);

    const handleAction = useCallback((type, ids) => {
        if (ids.length === 0) {
            showMessage(`Please select cookies to ${type.toLowerCase()}.`);
            return;
        }
        
        // 1. Trigger Animation for Delete/Reject
        if (type === 'Delete' || type === 'Reject') {
            setCookiesToDelete(ids);

            // 2. Perform deletion visually after animation (300ms)
            setTimeout(() => {
                setCookies(prevCookies => prevCookies.filter(cookie => !ids.includes(cookie.id)));
                setCookiesToDelete([]);
                showMessage(`${type}ed ${ids.length} cookie(s).`);
            }, 300);
        } else { 
            // Accept action (no animation delay needed)
            showMessage(`Accepted ${ids.length} cookie(s).`);
        }
    }, [showMessage]);

    // --- Render ---
    return (
        <div className="max-w-6xl mx-auto">

            <div className="bg-gray-900 border border-gray-700 shadow-2xl rounded-xl p-6 md:p-8">
                
                <header className="border-b border-gray-700 pb-4 mb-6">
                    <div className="flex flex-col sm:flex-row sm:items-center sm:gap-4">
                        <h1 className="text-3xl font-bold text-white">Cookie Dashboard</h1>
                        <span id="site-risk-badge" className={riskBadgeClass}>{riskBadgeText}</span>
                    </div>
                    <p className="text-gray-400 mt-1">Manage your privacy for the current site.</p>
                </header>
                
                {/* Info Banner */}
            {isBannerOpen && (
                <div id="info-banner" className="relative bg-sky-800 text-sky-100 p-4 rounded-lg mb-6 border border-sky-600 shadow-lg">
                    <button id="close-info-banner" className="absolute top-3 right-3 text-sky-200 hover:text-white" onClick={() => setIsBannerOpen(false)}>
                        <XCircle size={20} />
                    </button>
                    <h3 className="font-semibold text-lg mb-1">What are Cookies?</h3>
                    <p className="text-sm">
                        Cookies are small text files websites store on your browser. They are used to remember your preferences (like language), keep you logged in (session cookies), and, in some cases, track your activity across different sites for advertising. This dashboard helps you see and control them.
                    </p>
                </div>
            )}

                <InfoBar privacy_score={privacy_score} privacy_rank={privacy_rank} score_colour={score_colour} />

                <main className="grid grid-cols-1 lg:grid-cols-3 gap-8">
                    <CategoryPanel 
                        cookies={cookies} 
                        active_categories={active_categories} 
                        on_toggle={handleToggleCategory} 
                    />
                    <CookieTable 
                        cookies={filteredCookies} 
                        cookiesToDelete={cookiesToDelete}
                        onViewDetails={setSelectedCookieId}
                        onAction={handleAction} 
                    />
                </main>
            </div>
            
            {/* Floating Action Message */}
            <div 
                id="action-message" 
                className={`fixed bottom-10 right-10 bg-gray-700 text-white py-3 px-5 rounded-lg shadow-xl transition-all duration-300 ${actionMessage ? '' : 'translate-y-20 opacity-0'}`}
            >
                {actionMessage}
            </div>

            {/* Modal */}
            <CookieModal 
                cookie={currentCookie}
                isOpen={!!selectedCookieId}
                onClose={() => setSelectedCookieId(null)}
            />
        </div>
    );
}

export default CookieDashboard;