// frontend/src/components/CookieTable.js
import React, { useState, useMemo, useCallback, useEffect } from 'react';
import CookieRow from './CookieRow';

const CookieTable = ({ cookies, cookiesToDelete, onViewDetails, onAction }) => {
    const [selectedIds, setSelectedIds] = useState([]);

    // Reset selection if the cookie list changes significantly (e.g., after deletion/filtering)
    useEffect(() => {
        // Filter out deleted cookies from selection
        setSelectedIds(prev => prev.filter(id => cookies.some(c => c.id === id)));
    }, [cookies]);

    // --- Checkbox Handlers ---
    const handleSelectAll = useCallback((e) => {
        const checked = e.target.checked;
        setSelectedIds(checked ? cookies.map(c => c.id) : []);
    }, [cookies]);
    
    const handleSelectRow = useCallback((id, checked) => {
        setSelectedIds(prev => 
            checked ? [...prev, id] : prev.filter(i => i !== id)
        );
    }, []);

    // --- UI State Calculations ---
    const rowCount = cookies.length;
    const checkedCount = selectedIds.length;
    const isIndeterminate = checkedCount > 0 && checkedCount < rowCount;
    const isAllChecked = checkedCount === rowCount && rowCount > 0;

    const cookieRows = useMemo(() => {
        return cookies.map(cookie => (
            <CookieRow 
                key={cookie.id} 
                cookie={cookie} 
                isSelected={selectedIds.includes(cookie.id)} 
                onSelect={handleSelectRow}
                onViewDetails={onViewDetails}
                isDeleting={cookiesToDelete.includes(cookie.id)} // Pass state for animation
            />
        ));
    }, [cookies, selectedIds, handleSelectRow, onViewDetails, cookiesToDelete]);

    return (
        <div className="lg:col-span-2 bg-gray-800 p-6 rounded-lg border border-gray-700">
            <h2 className="text-xl font-semibold text-white mb-4">Active Cookies</h2>
            
            <div className="overflow-x-auto">
                <table className="w-full text-left table-auto">
                    <thead className="border-b border-gray-600 text-gray-400 uppercase text-sm">
                        <tr>
                            <th className="p-3">
                                <input 
                                    type="checkbox" 
                                    id="select-all" 
                                    onChange={handleSelectAll} 
                                    checked={isAllChecked} 
                                    ref={el => el && (el.indeterminate = isIndeterminate)} // Use ref for indeterminate state
                                />
                            </th>
                            <th className="p-3">Name</th>
                            <th className="p-3">Domain</th>
                            <th className="p-3">Category</th>
                            <th className="p-3">Insights</th>
                        </tr>
                    </thead>
                    <tbody id="cookie-table-body" className="divide-y divide-gray-700">
                        {cookieRows}
                    </tbody>
                </table>
            </div>
            
            {rowCount === 0 && (
                <div id="no-cookies-message" className="text-center p-8 text-gray-400">
                    <p>No cookies found or all cookies have been deleted.</p>
                </div>
            )}

            <div className="flex flex-col sm:flex-row gap-3 mt-6">
                <button 
                    id="btn-accept" 
                    className="flex-1 bg-green-600 hover:bg-green-700 text-white font-medium py-2 px-4 rounded-lg transition duration-150"
                    onClick={() => onAction('Accept', selectedIds)}
                >
                    Accept Selected
                </button>
                <button 
                    id="btn-reject" 
                    className="flex-1 bg-yellow-600 hover:bg-yellow-700 text-white font-medium py-2 px-4 rounded-lg transition duration-150"
                    onClick={() => onAction('Reject', selectedIds)}
                >
                    Reject Selected
                </button>
                <button 
                    id="btn-delete" 
                    className="flex-1 bg-red-600 hover:bg-red-700 text-white font-medium py-2 px-4 rounded-lg transition duration-150"
                    onClick={() => onAction('Delete', selectedIds)}
                >
                    Delete Selected
                </button>
            </div>
        </div>
    );
};

export default CookieTable;