// frontend/src/components/CategoryPanel.js
import React, { useMemo } from 'react';
import { PieChart, Pie, Cell, Tooltip, ResponsiveContainer } from 'recharts'; // Import Recharts
import { prepareChartData } from '../utils/cookieUtils';
import { pieChartData, categoryColors } from '../data/mockData';

const CategoryPanel = ({ cookies, active_categories, on_toggle }) => {
    
    // 1. Prepare data using the new helper
    const chartData = useMemo(() => prepareChartData(cookies), [cookies]);

    // 2. Toggle Items logic (remains mostly the same)
    const toggleItems = useMemo(() => {
        return Object.keys(pieChartData).map(category => {
            const isEssential = category === 'Essential';
            const isChecked = active_categories.includes(category);
            return (
                <div key={category} className="flex justify-between items-center">
                    <div className="flex items-center gap-3">
                        <span className="w-4 h-4 rounded-full" style={{ backgroundColor: categoryColors[category] }}></span>
                        <span className="font-medium text-sm text-gray-300">{category}</span>
                    </div>
                    <div className="relative inline-block w-10 mr-2 align-middle select-none transition duration-200 ease-in">
                        <input 
                            type="checkbox" 
                            id={`toggle-${category}`} 
                            className="toggle-checkbox absolute block w-6 h-6 rounded-full bg-white border-4 appearance-none cursor-pointer" 
                            checked={isChecked}
                            onChange={(e) => on_toggle(category, e.target.checked)}
                            disabled={isEssential}
                        />
                        <label htmlFor={`toggle-${category}`} className="toggle-label block overflow-hidden h-6 rounded-full bg-gray-600 cursor-pointer"></label>
                    </div>
                </div>
            );
        });
    }, [active_categories, on_toggle]);

    return (
        <div className="lg:col-span-1 bg-gray-800 p-6 rounded-lg border border-gray-700">
            <h2 className="text-xl font-semibold text-white mb-4">Cookie Categories</h2>

            {/* 3. THE NEW RECHARTS COMPONENT */}
            <div className="w-full h-64 mx-auto my-2"> 
                <ResponsiveContainer width="100%" height="100%">
                    <PieChart>
                        <Pie
                            data={chartData}
                            cx="50%"
                            cy="50%"
                            innerRadius={60} // Makes it a Donut Chart (looks modern)
                            outerRadius={80}
                            paddingAngle={5} // Adds space between slices
                            dataKey="value"
                        >
                            {chartData.map((entry, index) => (
                                <Cell key={`cell-${index}`} fill={entry.color} stroke="none" />
                            ))}
                        </Pie>
                        {/* Adds a nice hover tooltip automatically */}
                        <Tooltip 
                            contentStyle={{ backgroundColor: '#1f2937', borderColor: '#374151', color: '#fff' }}
                            itemStyle={{ color: '#fff' }}
                        />
                    </PieChart>
                </ResponsiveContainer>
            </div>

            <div className="space-y-5">
                {toggleItems}
            </div>

            {/* Privacy Tip remains here... */}
        </div>
    );
};

export default CategoryPanel;