// frontend/src/utils/cookieUtils.js
import React from 'react';
import { pieChartData } from '../data/mockData';

export const prepareChartData = (cookies) => {
    const counts = cookies.reduce((acc, cookie) => {
        acc[cookie.category] = (acc[cookie.category] || 0) + 1;
        return acc;
    }, {});

    // Convert the counts into the array format Recharts expects:
    // [{ name: 'Tracking', value: 5, color: '...' }, ...]
    return Object.keys(pieChartData).map(category => ({
        name: category,
        value: counts[category] || 0,
        color: pieChartData[category].color
    })).filter(item => item.value > 0); // Optional: Hide categories with 0 cookies
};


// --- BADGE & VULNERABILITY UTILITIES ---

export const getVulnerabilityExplanation = (vuln) => {
    switch (vuln.toLowerCase()) {
        case '3rd-party': return "<strong>3rd-Party:</strong> This cookie is set by a domain other than the one you are visiting, often for tracking or advertising.";
        case 'tracking': return "<strong>Tracking:</strong> This cookie is used to follow your activity across different websites to build a profile of your interests.";
        case 'missing secure': return "<strong>Missing 'Secure' Flag:</strong> This cookie can be sent over an unencrypted (HTTP) connection, making it easier for attackers to intercept.";
        case 'missing httponly': return "<strong>Missing 'HttpOnly' Flag:</b> This cookie can be accessed by client-side scripts (JavaScript), increasing the risk of XSS attacks.";
        default: return vuln;
    }
};

export const getInsightBadges = (vulnerabilities) => {
    if (vulnerabilities.length === 0) {
        return <span className="text-xs font-medium text-green-400">Secure</span>;
    }
    
    return vulnerabilities.map((vuln, index) => {
        let colorClass = 'bg-yellow-600 text-yellow-100';
        if (vuln.toLowerCase().includes('tracking') || vuln.toLowerCase().includes('3rd-party')) {
            colorClass = 'bg-red-600 text-red-100';
        }
        return (
            <span key={index} className={`inline-block rounded-full px-2 py-0.5 text-xs font-semibold ${colorClass}`}>
                {vuln}
            </span>
        );
    });
};

